:root {
  --blu: hsl(250, 50%, 50%);
  --blu-dark: hsl(250, 50%, 46%);
  --blu-light: hsl(250, 50%, 76%);
  --giallo: hsl(55, 70%, 60%);
  --rossa: hsl(353, 50%, 50%);
  --white: hsl(0, 100%, 100%);
  --black: hsl(0, 5%, 15%);
  --gray: hsl(0, 5%, 45%);
  --major: var(--blu);
  --minor: var(--rossa);
  --accent: var(--giallo);
  --light: var(--white);
  --dark: var(--black);
}
